//-----------------functions-----------------
.trans(){
    transition: all .3s ease-in-out;
}



//-----------------functions-----------------


//-----------------styles-----------------

body{
    min-width: 320px;
    font-family: 'Circe',Arial,sans-serif;
}
*{
    line-height: 1.55;
    margin: 0;
    padding: 0;
}
*:focus,*:active{
    outline: none;
    border: none;
}
input[type='number'] {
    -moz-appearance:textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.wrap{
    max-width: 1200px;
    margin: auto;
    padding: 0 15px;
}
.row{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.line{
    margin: 20px auto;
    width: 100px;
    height: 3px;
    background: #333333; 
}
.title{
    h4{
        text-align: center;
        font-size: 42px;
        font-weight: 600;
        text-transform: uppercase;
        color: @color;
    }
}
//-----------------styles-----------------
