
//top_menu
    #top_menu{
        width: 100%;
        top: 0;
        left: 0;
        background: rgba(255, 255, 255,1);
        z-index: 999;
        padding: 10px 0;
        position: fixed;
        color: @color;
        box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 3px;
        .wrap{
            .row{
                .logo{
                    a{
                        text-decoration: none;
                        justify-content: flex-start;
                        color: @color;
                        img{
                            margin-right: 10px;
                            width: 35px;
                        }
                        .description_logo{
                            h1{
                                text-transform: uppercase;
                                font-size: 16px;
                                font-weight: 400;
                                width: auto;
                            }
                        }
                    }   
                }
                .elements_top_menu{
                    nav{
                        margin-right: 25px;
                        ul{
                            display: flex;
                            align-items: center;
                            justify-content: center; 
                            li{
                                list-style: none;
                                margin: 0 20px;
                                position: relative;
                                .trans();
                                &:first-child{
                                    margin-left: 0;
                                }
                                &:last-child{
                                    margin-right: 0;
                                }
                                &::after{
                                    content: '';
                                    width: 100%;
                                    position: absolute;
                                    left: 0;
                                    bottom: 0;
                                    margin-bottom: -5px;
                                    height: 1px;
                                    opacity: 0;
                                    background: @color_1;
                                    .trans();
                                }
                                &.active{
                                    &::after{
                                        opacity: 1;
                                    }
                                    a{
                                        color: @color_1;
                                    }
                                }
                                &:hover{
                                    &::after{
                                        opacity: 1;
                                    }
                                    a{
                                        color: @color_1;
                                    }
                                }
                                a{
                                    font-size: 15px;
                                    .trans();
                                    text-transform: uppercase;
                                    font-weight: 600;
                                    color: @color;
                                    text-decoration: none;
                                }
                            }
                        }
                    }
                    .social_network{
                        display: flex;
                        flex-direction: row;
                        align-items: center;
                        justify-content: flex-end;
                        .row{
                            order: 2;
                            justify-content: flex-end;
                        }
                        .number{
                            order: 1;
                            font-size: 18px;
                            font-weight: 400;
                            color: @color_1;
                        }
                        a{
                            width: 30px;
                            display: block;
                            margin: 0 5px;
                            &:last-child{
                                margin-right: 0;
                            }
                            &:first-child{
                                margin-left: 20px;
                            }
                            img{
                                width: 100%;
                                height: auto;
                            }
                        }
                        .lang{
                            order: 3;
                            margin-left: 20px;
                            display: flex;
                            position: relative;
                            flex-direction: column;
                            align-items: flex-end;
                            >img{
                                cursor: pointer;
                                height: 50px;
                            }
                            .drop{
                                border-radius: 5px;
                                background: rgba(255, 255, 255,0.9);
                                padding: 0 15px;
                                position: absolute;
                                display: none;
                                bottom: 0;
                                transform: translateY(100%);
                                border: 1px solid rgba(0, 0, 0, 0.5); /* Добавляем треугольник */
                                &::after{
                                    content: ''; 
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 15px; 
                                    top: -20px; /* Положение треугольника */
                                    border: 10px solid transparent; /* Прозрачные границы */
                                    border-bottom: 10px solid rgba(0, 0, 0, 0.5) /* Добавляем треугольник */
                                }
                                &::before{
                                    content: ''; 
                                    position: absolute; /* Абсолютное позиционирование */
                                    right: 15px; 
                                    z-index: 5;
                                    top: -19px; /* Положение треугольника */
                                    border: 10px solid transparent; /* Прозрачные границы */
                                    border-bottom: 10px solid rgba(255, 255, 255,1); /* Добавляем треугольник */
                                }
                                a{
                                    color: #222;
                                    font-size: 18px;
                                    text-decoration: none;
                                    margin: 10px 0;
                                    border-radius: 5px;
                                    border: 1px @color solid;
                                    padding: 5px 15px;
                                    display: flex;
                                    align-items: center;
                                    justify-content: space-between;
                                    width: 85%;
                                    .trans();
                                    cursor: pointer;
                                    &:hover,&.active{
                                        background: @color;
                                        color: #fff;
                                    }
                                    img{
                                        width: auto;
                                        margin-right: 20px;
                                        height: 45px;
                                    }
                                }
                            }
                        }
                    }
                }
                .toggle_btn{
                    display: none;
                }
            }
        }
    }
//end-top_menu

//header
    header{
        padding-top: 80px;
        min-height: 600px;
        background: url(../img/header.jpg) 50% 50% / cover no-repeat;
        position: relative;
        z-index: 0;
        &::after{
            z-index: -1;
            content: '';
            width: 100%;
            height: 200px;
            bottom: 0;
            left: 0;
            position: absolute;
            background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%,rgba(255, 255, 255, 1) 90%);
        }
        .wrap{
            text-align: center;
            h2{
                padding: 50px 0 0;
                color: @color;
                font-weight: 600;
                font-size: 50px;
            }
            p{
                margin: 40px auto;
                font-size: 28px;
                font-weight: 600;
            }
            a{
                text-decoration: none;
                font-size: 30px;
                color: #222;
                font-weight: 600;
            }
            .row{
                margin-top: 60px;
                justify-content: center;
                button{
                    border: none;
                    padding: 18px 33px;
                    font-size: 16px;    
                    margin: 0 25px;
                    cursor: pointer;
                    border-radius: 5px;
                    text-transform: uppercase;
                    font-weight: 600;
                    .trans();
                    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 10px 0px;
                    &:hover{
                        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.3)
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                    &:first-child{
                        margin-left: 0;
                    }
                }
                .consultation{
                    background: #2DCC70;
                    color: #fff;
                    border: 3px #2DCC70 solid;

                }
                .detailed{
                    background: #fff;
                    color: @color;
                    border: 3px @color solid;
                }
            }
            .don_btn{
                margin: 30px auto 0;
                width: 50px;
                cursor: pointer;
                animation: movement 1.7s infinite ease;
                img{
                    width: 100%;
                    height: auto;
                }
            }
        }
    }
//end-header

