//news
    .news{
        padding: 50px 0;
        min-height: 250px;
        background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
        .wrap{
            .title{
                h4{
                    color: #222;
                }
                .line{
                    margin-top: 10px;
                    background: #888;
                }
            }
            .primary_news{
                align-items: flex-start;
                margin: 50px auto;
                .image{
                    width: 45%;
                    height: 350px;
                    overflow: hidden;
                    box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 5px;
                    img{
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                        object-position: center center;
                    }
                }
                .description_primal_news{
                    width: 50%;
                    text-align: left;
                    .title{
                        font-size: 30px;
                        font-weight: 400;
                        color: #222;
                        line-height: 1.4;
                    }
                    .date{
                        font-size: 14px;
                    }
                    .line{
                        margin: 20px 0 20px;
                        width: 80px;
                        background: @color;
                    }
                    .text{
                        color: #4e4e4e;
                        font-size: 18px;
                    }
                    .link{
                        display: inline-block;
                        margin-top: 25px;
                        font-size: 20px;
                        text-decoration: none;
                        color: @color;
                        padding-bottom: 2px;
                        .trans();
                        position: relative;
                        &::after{
                            content: '';
                            width: 100%;
                            .trans();
                            height: 2px;
                            background: rgba(0, 0, 0, 0);
                            position: absolute;
                            bottom: 0;
                            left: 0;
                        }
                        &:hover{
                            &::after{
                                background: @color;
                            }
                        }
                    }
                }
            }
            .title_last_news{
                text-align: left;
                margin-bottom: 40px;
                font-size: 25px;
                text-transform: uppercase;
                .line{
                    margin: 10px 0 0;
                    background: #888;
                }
            }
            .last_news{
                justify-content: space-between;
                align-items: flex-start;
                .box_news{
                    width: 30%;
                    margin: 0 20px 20px;
                    &:first-child{
                        margin-left: 0;
                    }
                    &:last-child{
                        margin-right: 0;
                    }
                    .image{
                        box-shadow: rgba(0, 0, 0, 0.5) 0px 1px 3px; 
                        width: 100%;
                        height: 215px;
                        overflow: hidden;
                        img{
                            object-fit: cover;
                            width: 100%;
                            height: 100%;
                        }
                    }
                    .description_last_news{
                        text-align: left;
                        .title{
                            margin-top: 10px;
                            font-size: 24px;
                            font-weight: 400;
                            line-height: 1.4;
                        }
                        .date{
                            font-size: 14px;
                        }
                        .line{
                            background: @color;
                            width: 50px;
                            margin: 10px 0 15px;
                        }
                        .text{
                            color: #4e4e4e;
                            line-height: 1.4;
                        }
                        .link{
                            display: inline-block;
                            margin-top: 10px;
                            font-size: 15px;
                            text-decoration: none;
                            color: @color;
                            padding-bottom: 2px;
                            .trans();
                            position: relative;
                            &::after{
                                content: '';
                                width: 100%;
                                .trans();
                                height: 2px;
                                background: rgba(0, 0, 0, 0);
                                position: absolute;
                                bottom: 0;
                                left: 0;
                            }
                            &:hover{
                                &::after{
                                    background: @color;
                                }
                            }
                        }
                    }
                }
            }
            .all_news{
                margin-top: 50px;
                display: flex;
                justify-content: center;
                a{
                    display: inline-block;
                    padding: 10px 30px;
                    background: rgba(0, 0, 0, 0);
                    border: 2px @color solid;
                    color: @color;
                    .trans();
                    font-size: 25px;
                    text-align: center;
                    text-transform: uppercase;
                    text-decoration: none;
                    &:hover{
                        background: @color;
                        border-color: rgba(0, 0, 0, 0);
                        color: #fff;
                    }
                }
            }
        }
    }
//end-news

