//end-contact_us
    .contact_us{
        margin: 0 0 50px;
        .wrap{
            .title{
                .description{
                    font-size: 25px;
                    color: @text;
                    text-align: center;
                }
            }
            .content{
                width: 100%;
                margin: 50px auto;
                form{
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    width: 100%;
                    .row{
                        position: relative;
                        width: 100%;
                        height: 220px;
                        >div{
                            height: 100%;
                            width: 100%;
                            display: flex;
                            justify-content: space-between;
                            flex-direction: column;
                            margin-right: 30px;
                            input{
                                font-size: 16px;
                                color: #86878b;
                                padding: 10px 15px; 
                                margin: 15px 0;
                                border: none;
                                border-bottom: 2px #86878b solid;
                                &:focus{
                                    border-bottom-color: @color;
                                }
                                &:first-child{
                                    margin-top: 0;
                                } 
                                &:last-of-type{
                                    margin-bottom: 0;
                                }
                            }
                        }
                        textarea{
                            font-size: 16px;
                            color: #86878b;                   
                            width: 100%;
                            height: 100%;
                            padding: 10px 15px; 
                            box-sizing: border-box;
                            border: none;    
                            resize: none; /* Запрещаем изменять размер */
                            margin-left: 30px;
                            border-bottom: 2px #86878b solid; 
                            &:focus{
                                border-color: @color;
                            }
                        }
                    }
                    .btn_send{
                        background: none;
                        color: @color;
                        border: 2px @color solid;
                        padding: 10px 30px;
                        font-size: 22px;    
                        line-height: 1.2;
                        text-align: left;
                        text-transform: uppercase;
                        margin: 45px 0 0;
                        cursor: pointer;
                        .trans();
                        &:hover{
                            border-color: rgba(0, 0, 0, 0);
                            background: @color;
                            color: rgb(255, 255, 255);
                        }
                    }
                }
            }
        }
    }
//contact_us



