//footer
    footer{            
        padding: 40px 0;
        box-sizing: border-box;
        background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(0,111,205,1) 100%); /* w3c */
        .wrap{
            .title_footer{
                margin-top: 60px;
                text-align: center;
                color: #fff;
                
                h1{
                    text-shadow: 1px 1px 2px #000;
                    font-size: 28px;
                }
            }
            .footer_nav{
                margin-top: 50px;
                nav{
                    ul{
                        display: flex;
                        align-items: center;
                        justify-content: center; 
                        li{
                            list-style: none;
                            margin: 0 20px;
                            position: relative;
                            .trans();
                            &:first-child{
                                margin-left: 0;
                            }
                            &:last-child{
                                margin-right: 0;
                            }
                            &::after{
                                content: '';
                                width: 100%;
                                position: absolute;
                                left: 0;
                                bottom: 0;
                                margin-bottom: -5px;
                                height: 1.5px;
                                opacity: 0;
                                background: #fff;
                                .trans();
                            }
                            &.active{
                                &::after{
                                    opacity: 1;
                                }
                                a{
                                    color: #fff;
                                }
                            }
                            &:hover{
                                &::after{
                                    opacity: 1;
                                }
                                a{
                                    color: #fff;
                                }
                            }
                            a{
                                font-size: 15px;
                                .trans();
                                text-shadow: 1px 1px 0px #000;
                                text-transform: uppercase;
                                font-weight: 600;
                                color: #fff;
                                text-decoration: none;
                            }
                        }
                    }
                }
            }
            .other{
                margin-top: 50px;
                align-items: flex-start;
                .istagram,.facebook{
                    width: 100%;
                    height: 250px;
                    background: #fFF;
                }
                .language{
                    margin: 0 50px;
                    width: 50%;
                    justify-content: center;
                    align-items: flex-start;
                    a{
                        width: 80px;
                        margin: 0 20px;
                        text-align: center;
                        align-items: center;
                        flex-direction: column;
                        text-decoration: none;
                        font-size: 20px;
                        position: relative;
                        text-shadow: 1px 1px 0px #000;
                        color: #fff;
                        &:last-child{
                            margin-right: 0;
                        }
                        &:first-child{
                            margin-left: 0;
                        }
                        &::after{
                            content: '';
                            width: 100%;
                            height: 2px;
                            position: absolute;
                            left: 0;
                            bottom: -5px;
                            background: #fff;
                            width: 0;
                            .trans();
                        }
                        &:hover{
                            &::after{
                                width: 50%;
                                left: 50%;
                                transform: translateX(-50%);
                            }
                        }
                        &.active{
                            &::after{
                                width: 100%;
                            }
                        }
                        img{
                            width: 100%;
                            height: auto;
                        }
                    }
                }
            }
        }
    }
//end-footer








//animate

    @keyframes movement {
        0%   {
            -moz-transform: translateY(0);
            -ms-transform: translateY(0);
            -webkit-transform: translateY(0);
            -o-transform: translateY(0);
            transform: translateY(0);
          }
          50%   {
            -moz-transform: translateY(-7px);
            -ms-transform: translateY(-7px);
            -webkit-transform: translateY(-7px);
            -o-transform: translateY(-7px);
            transform: translateY(-7px);
          }
          55%   {
            -moz-transform: translateY(-7px);
            -ms-transform: translateY(-7px);
            -webkit-transform: translateY(-7px);
            -o-transform: translateY(-7px);
            transform: translateY(-7px);
          }
          100%   {
            -moz-transform: translateY(0);
            -ms-transform: translateY(0);
            -webkit-transform: translateY(0);
            -o-transform: translateY(0);
            transform: translateY(0);
          }
    }


//end-animate